import React from 'react'

export default function paymentgateways() {
  return (
    <div><section className="bg-white shadow-lg rounded-lg p-6 mb-8">
    <h3 className="text-xl font-bold text-gray-800 mb-4">Payment Gateways</h3>
    <p className="text-gray-600 mb-6">
      Breakdown of payment methods used by customers.
    </p>
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
      {[
        {
          method: "Credit Card",
          percentage: "55%",
          bg: "bg-blue-100",
          color: "text-blue-600",
        },
        {
          method: "PayPal",
          percentage: "30%",
          bg: "bg-green-100",
          color: "text-green-600",
        },
        {
          method: "Bank Transfer",
          percentage: "10%",
          bg: "bg-yellow-100",
          color: "text-yellow-600",
        },
        {
          method: "Other",
          percentage: "5%",
          bg: "bg-gray-100",
          color: "text-gray-600",
        },
      ].map(({ method, percentage, bg, color }, index) => (
        <div
          key={index}
          className={`p-4 rounded-lg shadow-md flex items-center justify-between ${bg} ${color}`}
        >
          <h4 className="text-lg font-semibold">{method}</h4>
          <span className="text-xl font-bold">{percentage}</span>
        </div>
      ))}
    </div>
  </section>
  </div>
  )
}
