import React from 'react'

export default function performanceinsights() {
  return (
<section className="bg-white shadow-lg rounded-lg p-6 mb-8">
        <h3 className="text-xl font-bold text-gray-800 mb-4">Performance Insights</h3>
        <p className="text-gray-600 mb-6">
          Dive deeper into how your store is performing with actionable insights.
        </p>
        <ul className="space-y-4">
          {[
            "High-performing products: Focus on restocking your top sellers.",
            "Customer engagement: Increase email open rates with personalized campaigns.",
            "Low conversion rates: Optimize product pages for better user experience.",
          ].map((insight, index) => (
            <li
              key={index}
              className="bg-gray-50 p-4 rounded-lg shadow-md text-gray-700 font-medium"
            >
              {insight}
            </li>
          ))}
        </ul>
      </section>
  )
}
