
import React from 'react';
import { BarChart2, TrendingUp } from 'lucide-react';
import {  Bar, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';
import { BarChart } from 'recharts';
export default function SalesOverview() {
  const salesData = [
    { month: 'Jan', sales: 5000 },
    { month: 'Feb', sales: 5200 },
    { month: 'Mar', sales: 5400 },
    { month: 'Apr', sales: 5600 },
    { month: 'May', sales: 5800 },
    { month: 'Jun', sales: 6000 },
  ];

  return (
    <section className="bg-white shadow-lg rounded-lg p-6 mb-8">
      <h3 className="text-xl font-bold text-gray-800 mb-4">Sales Overview</h3>
      <p className="text-gray-600 mb-6">
        Analyze weekly, monthly, and yearly sales trends to understand your growth trajectory.
      </p>

      <div className="flex justify-between items-center mb-4">
        <div className="flex items-center space-x-2">
          <BarChart2 className="text-gray-600" />
          <h4 className="text-lg font-semibold">Weekly Sales</h4>
        </div>
        <span className="text-sm text-gray-500">Last week: $5,200</span>
      </div>

      {/* Sales Graph Section */}
      <ResponsiveContainer width="100%" height={300}>
        <BarChart data={salesData}>
          <XAxis dataKey="month" />
          <YAxis />
          <Tooltip />
          <Bar dataKey="sales" fill="#4caf50" />
        </BarChart>
      </ResponsiveContainer>

      <div className="flex justify-between items-center mt-4">
        <div className="flex items-center space-x-2">
          <TrendingUp className="text-green-500" />
          <h4 className="text-lg font-semibold">Growth</h4>
        </div>
        <span className="text-sm text-gray-500">+8% compared to last month</span>
      </div>
    </section>
  );
}
