import React from 'react';
import { CheckCircle, AlertTriangle, XCircle } from 'lucide-react'; // Import Lucide icons

export default function InventoryStatus() {
  return (
    <section className="bg-white shadow-lg rounded-lg p-6 mb-8">
      <h3 className="text-xl font-bold text-gray-800 mb-4">Inventory Status</h3>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {[
          {
            item: 'T-Shirts',
            stock: '120 units',
            status: 'In Stock',
            bg: 'bg-green-100',
            color: 'text-green-600',
            icon: <CheckCircle className="text-green-600" />,
          },
          {
            item: 'Sneakers',
            stock: '15 units',
            status: 'Low Stock',
            bg: 'bg-yellow-100',
            color: 'text-yellow-600',
            icon: <AlertTriangle className="text-yellow-600" />,
          },
          {
            item: 'Caps',
            stock: '0 units',
            status: 'Out of Stock',
            bg: 'bg-red-100',
            color: 'text-red-600',
            icon: <XCircle className="text-red-600" />,
          },
        ].map(({ item, stock, status, bg, color, icon }, index) => (
          <article
            key={index}
            className={`p-4 rounded-lg shadow-md flex items-center justify-between ${bg} ${color} transition-transform transform hover:scale-105`}
            aria-labelledby={`inventory-item-${item}`}
          >
            <div>
              <h4 id={`inventory-item-${item}`} className="text-lg font-semibold flex items-center">
                {icon}
                <span className="ml-2">{item}</span>
              </h4>
              <p className="text-sm">{status}</p>
            </div>
            <span className="text-lg font-bold">{stock}</span>
          </article>
        ))}
      </div>
    </section>
  );
}
