import React from 'react'

export default function weeklyperformance() {
  return (
    <section className="bg-white shadow-lg rounded-lg p-6 mb-8">
        <h3 className="text-xl font-bold text-gray-800 mb-4">Weekly Performance</h3>
        <p className="text-gray-600 mb-6">
          Track this week’s sales and revenue growth in comparison to previous weeks.
        </p>
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <div className="w-full h-64 bg-gray-100 rounded-lg flex items-center justify-center text-gray-400">
            Weekly Sales Chart Placeholder
          </div>
          <ul className="space-y-4">
            {[
              {
                day: "Monday",
                sales: "500 units",
                revenue: "$7,500",
              },
              {
                day: "Tuesday",
                sales: "450 units",
                revenue: "$6,750",
              },
              {
                day: "Wednesday",
                sales: "480 units",
                revenue: "$7,200",
              },
              {
                day: "Thursday",
                sales: "530 units",
                revenue: "$7,950",
              },
            ].map(({ day, sales, revenue }, index) => (
              <li
                key={index}
                className="bg-gray-50 p-4 rounded-lg shadow-md flex justify-between items-center"
              >
                <div>
                  <h4 className="text-lg font-semibold text-gray-700">{day}</h4>
                  <p className="text-sm text-gray-500">{sales}</p>
                </div>
                <span className="text-lg font-bold text-gray-800">{revenue}</span>
              </li>
            ))}
          </ul>
        </div>
      </section> 
  )
}
